import server from '../../server.js';
export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			modalName: null,
			postcontent:'',
			huati:"",
			nr:'',
			huatiname:"选择话题",
			oosArr: [],
				// 阿里云oos相关配置
			upImgOos: {
				aliConfig: {
					// 阿里云oos上传key_secret(后端传)
					AccessKeySecret: 'Us5V1E9tD2bSYFWYWYsHES4lNdAXRi',
					// 阿里云oos上传key_id(后端传)
					OSSAccessKeyId: 'LTAI9dyLJEiv09Ss',
					// 阿里云oos上传目录(必须存在)
					oosDirectory: 'angeli-image',
					// 阿里云上传url
					url: 'https://sz.oss.data.angeli.top/'
				},
				// 是否开启notli(开启的话就是选择完直接上传，关闭的话当count满足数量时才上传)
				notli: true,
				// 图片数量
				count: 9,
				upTextDesc:"",
				// 上传图片背景修改 
				upBgColor: '#EFEFF4',
				// 上传icon图标颜色修改(仅限于iconfont)
				upIconColor: '#fff',
				// 上传svg图标名称
				upSvgIconName: 'icon-addicon',
				isAddImage:true
			}
			
		}
	},




















	onShow:function(){
		console.log(server.postClass)
		this.huati=server.postClass.ClassId;
		this.huatiname=server.postClass.ClassName
		console.log(server.system)
	},
	onLoad: function(e) {







		uni.getSystemInfo({
		    success: function (res) {
				var systemjson={
					phonebrand:res.brand+res.model,
					phonesystem:res.system
				}
				server.system=JSON.stringify(systemjson);
				console.log(server.system)
		    }
		});
		
		
	},
	methods: {
		getClassList:function(){
			uni.navigateTo({
				url: '../huati/huati'
			})
		},
		uImageTap() {
			this.$refs.uImage.uploadimage(this.upImgOos);
		},
		// 删除图片 -2019/05/12(本地图片进行删除)
		async delImgInfo(e) {
			console.log('你删除的图片地址为:', e, this.oosArr.splice(e.index, 1));
		},
		// 阿里云
		async upOosData(e) {
			// 上传图片数组
			let arrImg = [];
			for (let i = 0, len = e.length; i < len; i++) {
				try {
					if (e[i].path_server != "") {
						await arrImg.push(e[i].path_server.split(','));
					}
				} catch (err) {
					console.log('上传失败...');
				}
			}
			// 图片信息保存到data数组
			this.oosArr = arrImg;
		
			// 可以根据长度来判断图片是否上传成功. 2019/4/11新增
			if (arrImg.length == this.upImgOos.count) {
				uni.showToast({
					title: `上传成功`,
					icon: 'none'
				});
			}
		},
		// 获取上传图片阿里云
		
		textareaAInput:function(e){
			this.postcontent = e.target.value 
		},
		sendpost:function(e){
			console.log(this.postcontent);
			if(this.postcontent=="null" ||this.postcontent==""){
				uni.showToast({
					title: "请输入文字内容！",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.postcontent.length>2000 || this.postcontent.length<10 ){
				uni.showToast({
					title: "内容最少10个字，最多2000个字",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(this.huati==null || this.huati==false||this.huati==0||this.huati=='选择话题'){
				uni.showToast({
					title: "你还没有选择话题",
					position:'bottom',
					icon:'none',
					mask:true
				})
				return;
			}
			if(!this.huati){
				this.huati=1
			}
			uni.showLoading({
				title: '发送帖子中..',
				mask:true
			});
			
			console.log(server.system)
			
			uni.request({
				method:'POST',
				url: 'https://api.angeli.top/post.php?type=addPost',
				data: {
					imageList: this.oosArr.join().split(','),
					txt:this.postcontent,
					huati:this.huati,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'system':server.system
				},
				success: (res) => {
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none',
						duration:2000,
						mask:true
					});
					if(res.data.code=="2"){
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none',
							duration:2000,
							mask:true
						});
					}
					if(res.data.code=="1"){
						setTimeout(function () {
							uni.hideLoading()
							uni.redirectTo({
								url: '../Home/Home?type=plusPost'
							})
						}, 2000);
					}
				},
				complete() {

				}
			});
			
		}
	}
}

